import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { getInitialSettings } from "../actions/setting-form-action";
const data = {
  weatherCity: 'Penza',
  timeInterval: '300',
}

class SettingForm extends Component {

  onSubmit(values) {
    this.props.callbackFromParent(values);
  }

  timeHandleChange=(e)=>{
    console.log(e.target.value)
  };

  render() {
    const { handleSubmit } = this.props;
    return (
      <form onSubmit={handleSubmit(this.onSubmit.bind(this))}>
        <div className='form-group'>
          <label>City</label>
          <Field className="form-control" name="weatherCity" component="input" type="text"/>
        </div>
        <div className='form-group'>
          <label>Favorite Color</label>
            <Field className="form-control" name="timeInterval" component="select"
                   onChange={this.timeHandleChange}>
              <option></option>
              <option value="300">5 min</option>
              <option value="600">10 min</option>
              <option value="1200">20 min</option>
            </Field>
        </div>
        <button type="submit" className="btn btn-primary">Submit</button>
      </form>
    );
  }
}

SettingForm = reduxForm({
  form: 'settings-form'
})(SettingForm);


SettingForm = connect(
  state => ({
    initialValues: data
  }),
  { load: getInitialSettings }
)(SettingForm);

export default SettingForm


